import Vue from 'vue'
import VueRouter from 'vue-router'

// 导入 login
// import Login from '../components/Login.vue'
const Login = () =>
  import(/* webpackChunkName: "login_home_welcome" */ '../components/Login.vue')
// 导入 Home
// import Home from '../components/Home.vue'
const Home = () =>
  import(/* webpackChunkName: "login_home_welcome" */ '../components/Home.vue')
// 导入 Welcome
// import Welcome from '../components/Welcome.vue'
const Welcome = () =>
  import(/* webpackChunkName: "login_home_welcome" */ '../components/Welcome.vue')

// 导入 Users
// import Users from '../components/user/Users.vue'
const Users = () =>
  import(/* webpackChunkName: "users_rights_roles" */ '../components/user/Users.vue')
// 导入 Rights
// import Rights from '../components/right/Rights.vue'
const Rights = () =>
  import(/* webpackChunkName: "users_rights_roles" */ '../components/right/Rights.vue')
// 导入 Roles
// import Roles from '../components/right/Roles.vue'
const Roles = () =>
  import(/* webpackChunkName: "users_rights_roles" */ '../components/right/Roles.vue')

// 导入 GoodsCate
// import GoodsCate from '../components/goods/GoodsCate'
const GoodsCate = () =>
  import(/* webpackChunkName: "goodscate_params" */ '../components/goods/GoodsCate')
// 导入 Params
// import Params from '../components/goods/Params'
const Params = () =>
  import(/* webpackChunkName: "goodscate_params" */ '../components/goods/Params')

// 导入 GoodsLIst
// import GoodsList from '../components/goods/GoodsList'
const GoodsList = () =>
  import(/* webpackChunkName: "goodslist_add" */ '../components/goods/GoodsList')
// 导入 Add
// import Add from '../components/goods/Add'
const Add = () =>
  import(/* webpackChunkName: "goodslist_add" */ '../components/goods/Add')

// 导入 Orders
// import Orders from '../components/oreders/Orders'
const Orders = () =>
  import(/* webpackChunkName: "orders_reports" */ '../components/oreders/Orders')
// 导入 Reports
// import Reports from '../components/report/Reports'
const Reports = () =>
  import(/* webpackChunkName: "orders_reports" */ '../components/report/Reports')

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/login' },
  { path: '/login', component: Login },
  {
    path: '/home',
    redirect: '/welcome',
    component: Home,
    children: [{
      path: '/welcome',
      component: Welcome
    }, {
      path: '/users',
      component: Users
    }, {
      path: '/rights',
      component: Rights
    }, {
      path: '/roles',
      component: Roles
    }, {
      path: '/categories',
      component: GoodsCate
    }, {
      path: '/params',
      component: Params
    }, {
      path: '/goods',
      component: GoodsList
    }, {
      path: '/goods/add',
      component: Add
    }, {
      path: '/orders',
      component: Orders
    }, {
      path: '/reports',
      component: Reports
    }]
  }
]

const router = new VueRouter({
  routes
})

// 挂载导航守卫
router.beforeEach((to, from, next) => {
  if (to.path === '/login') return next()
  // 获取 token
  const tokenStr = window.sessionStorage.getItem('token')
  // 根据 token判断是否有权限
  if (!tokenStr) return next('/login') // 没有权限，强制跳转
  next()
})
export default router
